@import "_reset.scss";
@import "_vars.scss";
@import "_base.scss";
@import "_mixins.scss";



@mixin roundTopCorners($int){
    border-top-left-radius: $int;
    border-top-right-radius: $int;
}


 body {
    background-color: $flatred;
 }






.no-scroll{
    overflow: hidden;
}


.wrap{
    
    transition: background-color 0.5s ease;

    
    
    
}



.content-wrapper{
    display: flex; 
    flex-direction: column;

    
    width: 97%;
    margin: 0 auto;
    
    
}

.tab-content-container{

    margin: 0 auto;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    width: 100%;

    @media (orientation:landscape){
        
        top: 5%;
    }

    @media #{$tablet-view}{
        width: 70%
    }

    @media #{$desktop-view}{
        top: 20%;
        width: 60%;
    }
}

.container{
    
    margin: 0 auto;
    

    @media #{$tablet-view}{
        width: 80%;
    }

    @media #{$desktop-view}{
        width: 80%;
    }
}



.logo-container{
    display: none;
    position: fixed;
    top: 0;
    width: 100%;
    border-bottom: 1px solid $baseblack;
    z-index: 10;
    background-color: rgb(245, 245, 245);

    
    
    
    

    @media #{$tablet-view}{
        position: static;
    }

    @media #{$desktop-view}{
        position: static;
    }
}

.logo{
    width: 15%;
    height: auto;
    display: block;
    margin: 0 auto;
    padding: 5px 0;
    transition: width 1s;
    
}

.logo--size{
    width: 10%;
}

.hide-fixed-container{
    display: none;
}


.fa-caret-right,
.fa-chevron-right{
    pointer-events: none;
    animation: animatedCaretReverse .6s $cbc forwards;
}

.fa-chevron-right{
    width: .4em !important; 
}


.fa-times{
    stroke-width: 1px;
}

.svg-inline--fa{
    vertical-align: -.25em;
}

.svg-inline--fa.fa-w-16{
    width: 2em;

    @media #{$tablet-view}{
        width: 5em;
        height: 5em
    }

    
    @media #{$desktop-view}{
        width: 10em;
        height: 10em
    }
    
}

.close-modal-btn{
    position: absolute;
    top: -80px;
    right: 10px;
    
    
    line-height: 1.5rem;
    text-align: center;
    cursor: pointer;
    border-radius: 0.25rem;
    background-color: lightgray;
    padding: 0.5em 1em;

    @media #{$l-mobile-view}{
        top: -20px;
        right: 10px;
    }

    @media #{$tablet-view}{
        top: -20px;
        right: 0;
        padding: 0.5em 2em;
    }

    &:hover{
        background-color: darkgray;
        color: white;
    }

    &--tran{
        top: -50px;
    }
}



.mood{
    display: flex;
    align-items: center;
    flex-direction: column;
    

    &__input-container{
        position: relative;
        width: 100%;
        display: flex;
        justify-content: center;
        border-bottom: 1px solid #EFA09B;

        


    }

    &__input-search{
        width: 100%;
        padding-bottom: 5px;
    }

    &__header{
        

        
        font-size: 3rem;
        align-self: flex-start;
        
        
        margin: 1.5em 0;
        width: 100%;
        overflow-wrap: break-word;
        color: white;
        font-weight: $f-light;
        
        @media #{$tablet-view}{
            font-size: 4.5rem;
            margin: 2em 0;
        }

        @media #{$desktop-view}{
            font-size: 6rem;
            margin: 1em 0;
        }
    }

    &__form{
        width: 100%;

        @media #{$desktop-view}{
            
        }
        
    }

    &__val{
        
        border: none;
        border-radius: 10px;
        padding: 10px 0 10px 5px;
        width: 100%;
        background-color: $flatred;
        color: white;
        font-size: 1.8em;

         

        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            color: #EFA09B;
        }
        &::-moz-placeholder { /* Firefox 19+ */
            color: #EFA09B;
        }
        &:-ms-input-placeholder { /* IE 10+ */
            color: #EFA09B;
        }
        &:-moz-placeholder { /* Firefox 18- */
            color: #EFA09B;
        }

        &:focus{
            outline: none;
        }

        @media #{$tablet-view}{
            
            
            font-size: 3.5em;
            border-radius: 0;
            padding-left: 5px;
            
        }

        @media #{$desktop-view}{

            &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                font-size: 4rem;
                color: #EFA09B;
              }
              &::-moz-placeholder { /* Firefox 19+ */
                color: #EFA09B;
              }
              &:-ms-input-placeholder { /* IE 10+ */
                color: #EFA09B;
              }
              &:-moz-placeholder { /* Firefox 18- */
                color: #EFA09B;
              }
