    }

    &__i-container{
        display: inline-block;
        position: relative;
        bottom: -15px;

        
        @media #{$desktop-view}{
            
        }
    }

    &__i-btn{
        
        
        cursor: pointer;
        
        padding: 0;
        border: none;
        background: none;   

        & path{
            fill: #EFA09B
