            border: 1px solid $baseblack;
            border-radius: 5px;
            margin-bottom: .6em;
            
            font-size: .8em;
            
            background-color: white;
            padding: 1em 1.8em 1em .8em;
            
            
            vertical-align: center;
            

            &:hover{
                background-color: rgba(0, 0, 0, 0.2);
                color: white;
            }

            &:hover > svg{
                animation: animatedRemoveGenreTag .5s $cbc;
            }
        }

        &__x{
            position: absolute;
            right: 5px;
        }
    }

   
}



.genres{

