            align-self:stretch;
            font-size: .8em;
            margin-bottom: 10px;
        }
        

        &__list{
            display: flex;
            flex-flow: row wrap;
            
            height: 90px;
            max-height: 90px;


            @media #{$tablet-view}{
                
                
                
            }
    
            @media #{$desktop-view}{
                
