            padding: 5px 30px 5px 10px;

            &:hover{
                background-color: rgba(0, 0, 0, 0.2);
            }

            &:hover > svg{
                animation: animatedRemoveGenreTag .5s $cbc;
            }

            & svg{
                pointer-events: none;
            }
            
            
            @media #{$tablet-view}{
                padding: 5px 30px 5px 10px;
                
                
            }
    
            @media #{$desktop-view}{
                padding: 5px 30px 5px 10px;
                
            }
        }

        &__x{
            position: absolute;
            right: 5px;

        }

        &__clear{
            border-radius: 20px;
            border: 1px solid $baseblack;
            padding: .8em 1.2em;
            background-color: white;
            text-align: center;
            width: 100%; 
            margin-top: 10px;
            align-self: flex-end;

            @media #{$tablet-view}{
                padding: .5em 1.2em;
                width:150px;
                min-width: 150px;
                margin-top: 0;
            }
    
            @media #{$desktop-view}{
