            background-size: 200% 100%;
            background-position:right bottom;
            transition:all 0.4s ease-in-out;

            
            display: flex;
            justify-content: center;
            flex-direction: column;
            text-align: center;

            @media #{$tablet-view}{
                width: 25%;
                height: 80px;
            }

            &:hover{
                background-color: rgba(206, 206, 206, 0.8);
            }
        }
    }

    &-toggle{
        
        background-position: 0% 100%;
        
        
        color: white;
        
    }

    
    
    
    

    &-active-animation{
        animation: animatedCaret .6s $cbc forwards;
    }
    
    &-show{
        display: block;
        -webkit-animation: animateDrop 0.2s $cbc;
        animation: animateDrop 0.2s $cbc;
        height: 85vh;
        overflow: auto;

        @media #{$tablet-view}{
            height: initial;   
        }

        
        
        
        
        
        
        
        
        
        
        
    }
}

.no-transition {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -o-transition: none !important;
    transition: none !important;
}

.no-transform{
    -webkit-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -o-transform: none !important;
    transform: none !important;
}
.no-animation{
    -webkit-animation: none !important;
    -moz-animation: none !important;
    -ms-animation: none !important;
    -o-animation: none !important;
    animation: none !important;
}


@keyframes animatedRemoveGenreTag {
    0% {
        transform: rotate(0deg) scale(1);
    }

    25%{
        transform: rotate(10deg);
    }
    50%{
        transform: rotate(-10deg) scale(1.5);
    }
    100%{
        transform: rotate(0deg) scale(1);
    }
}


@keyframes animatedCaret {
    0% {
        transform: rotate(0deg);
    }

    25%{
        transform: rotate(100deg);
    }
    50%{
        transform: rotate(80deg);
    }
    100%{
        transform: rotate(90deg);
    }
}

@keyframes animatedCaretReverse {
    0% {
        transform: rotate(90deg);
    }

    25%{
        transform: rotate(-10deg);
    }
    50%{
        transform: rotate(10deg);
    }
    100%{
        transform: rotate(0deg);
    }
}

@-webkit-keyframes animateDrop {
    from {
        transform: translate(0, -50px); 
        opacity:0
    } 
    to {
        transform: translate(0, 0);  
        opacity:1
    }
}

@keyframes animateDrop {
    from {
        transform: translate(0, -50px); 
        opacity:0
    } 
    to {
        transform: translate(0, 0);  
        opacity:1
    }
}





.tracks{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    background-color: #F2F2F2;
}

.track {
    background-color: #fff;
    box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    width: 90%;
    position: relative;
    margin: 40px 0;


    @media #{$l-mobile-view} and (orientation: landscape){
        width: 45%;
    }
    
    @media #{$tablet-view}{
        width: 42%;
    }

    @media #{$desktop-view}{
        width: 30%;
    }


    &__pl-add{
        position: absolute;
        top: -20px;
        right: -10px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        cursor: pointer;
        
        

        @media #{$tablet-view}{
            top: -20px;
            right: -20px;
            width: 50px;
            height: 50px;
            transform: scale(1) rotate(-90deg);
        }


        &:hover{      

            
                transform: scale(1.2) rotate(90deg);
                transition: transform .2s $cbc;
            
        }

    }

    &__pl-add svg{
        height: 40px;
        width: 40px;

        @media #{$tablet-view}{
            height: 50px;
            width: 50px;
        }
    }

    &__img{
        cursor: pointer;
        width: 100%;
        height: 300px;
        border-radius: 5px 5px 0 0;
    }


    &__name{
        text-align: center;
        height: 77px;
        font-weight: f-heavy;
        overflow: hidden;
        line-height: 1.1em;
        margin-bottom: 10px;
    
    
    
    
    
    
      
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
        

    }


    &__arist{
        
        text-align: center;
        font-weight: $f-normal;
        margin-top: auto;
        margin-bottom: 20px;
    }

    &__progress-bar{
        height: 20px;
        
        background-color: $flatred;
    }

    & .media-controls{
        width: 100%;
        background: linear-gradient(to left, white 50%, $color--green 50%);
        background-size: 200% 100%;
        background-position:right bottom;
        transition:all 0.2s ease-in-out;

        &-toggle{
            background-position: 0% 100%;
        }

        &-toggle .pause-ani{
