.tabs {
    position: relative;   
    
    clear: both;
    margin: 25px 0;
  }
  .tab {
    
    float: left;
  }
  .tab > label {
    background: #eee; 
    padding: 10px; 
    border: 1px solid rgba(204, 204, 204, 1); 
    margin-left: -1px; 
    position: relative;
    left: 1px; 

    
    
    
    
  }
  .tab [type=radio] {
    display: none;   
  }
  .content {
    position: absolute;
    top: 28px;
    left: 0;
    background: white;
    right: 0;
    bottom: 0;
    padding: 20px;
    border: 1px solid #ccc; 
    height: 60vh;
    border-radius: 5px;

    @media #{$l-mobile-view} and (orientation: landscape){
        height: 80vh;
    }

    @media #{$tablet-view}{
        height: 40vh;
        
    }

    @media #{$tablet-view} and (orientation: landscape){
        height: 60vh;
        
    }


  }
  [type=radio] ~ label{
    @include roundTopCorners(5px);
  }

  [type=radio]:checked ~ label {
    background: white;
    border-bottom: 1px solid white;
    z-index: 2;
    
  }
  [type=radio]:checked ~ label ~ .content {
    z-index: 1;
  }
  

.svg-try{
    position: absolute;
    
    

    &-half{
         & path{
            stroke-dasharray: 280 280;
            stroke-dashoffset: 280;
            animation: draw 2s ease-in-out infinite;
         }
    }

    &-pause{
        top: 39px;

        & path{
            stroke-dasharray: 280 280;
            stroke-dashoffset: 280;
            animation: drawReverse 2s ease-in-out infinite;
         }
    }

    &-full{
        & path{
            stroke-dasharray: 280 280;
            stroke-dashoffset: 280;
            animation: draw 2s ease-in-out infinite;
         }

    }
}

@keyframes draw {
    from {
      stroke-dashoffset: 280;
    }
  
    to {
      stroke-dashoffset: 0;
    }
  }

  @keyframes drawReverse {
    from {
      stroke-dashoffset: 0;
    }
  
    to {
      stroke-dashoffset: 280;
    }
  }



  @mixin no-dash-animation{
    -webkit-stroke-dasharray: none !important;
    -moz-stroke-dasharray: none !important;
    -o-stroke-dasharray: none !important;
    stroke-dasharray: none !important;
  }
  
  .media-controls{
      
    cursor: pointer;
    
    
  
    &__play{
      stroke-dasharray: 280 280;
      animation: drawPlayReverse .2s $cbc forwards;
      pointer-events: none;
    
    }
    
    &__pause{
      stroke-dasharray: 40 280;
      animation: drawPauseReverse .2s $cbc forwards;
      pointer-events: none;
    }
  }
  
  @keyframes drawPlay {
      0% {
        stroke-dashoffset: 0;
      }
    
      100% {
        stroke-dashoffset: 240;
      }
    }
  
  
  @keyframes drawPause {
      0% {
        stroke-dashoffset: -200;
  
          }
      75%{
            stroke-dashoffset: 0;
      }
      90%{
        transform: translateY(0px);
      }
  
      100% {
        
        transform: translateY(6.5px);
   
      }
    }
  
  .play-ani{
    animation: drawPlay 0.2s $cbc forwards;
  }
  
  .pause-ani{
    animation: drawPause 0.3s $cbc forwards;
  }
  
  @keyframes drawPauseReverse {
      0% {
        stroke-dashoffset: 0;
  
          }
      100% {
        stroke-dashoffset: -100;
      }
    }
  
  @keyframes drawPlayReverse {
      0% {
        stroke-dashoffset: 242;
          }
      100% {
        stroke-dashoffset: 100;
      }
    }
  
  .inactive-dash{
    @include no-dash-animation;
    
    &-pause{
        
      display: none;
    }
  }
  

