*,
*:before,
*:after{
        -webkit-border-sizing: border-box;
              -moz-box-sizing: border-box;
                   box-sizing: border-box;
                //    transition: all .3s ease-in-out
}

h1{
    font-size: 2.4rem;
}

h2{
    font-size: 2.2rem;
}

h3{
    font-size: 2rem;
}

h4{
    font-size: 1.8rem;
}

h5{
    font-size: 1.6rem;
}

h6{
    font-size: 1.4rem;
}

p{
    font-size: 1rem;
}

a{
    text-decoration: none;
}

body{
    color: rgb(90, 90, 90);
    font-family: 'Nunito', sans-serif;
    font-weight: $f-light;
    text-rendering: optimizeLegibility;
}


